.marker {
    border-width: 3px;
    border-style: solid;
    border-radius: 50%;
    cursor: pointer;
}

.verysmall {
    background-color: rgba(243, 219, 5, 0.5);
    border-color: rgba(243, 219, 5, 0.5);
}

.small {
    background-color: rgba(199, 171, 11, 0.7);
    border-color: rgba(199, 171, 11, 0.7);
}

.verynormal {
    background-color: rgba(253, 169, 43, 0.7);
    border-color: rgba(253, 169, 43, 0.7);
}

.normal {
    background-color: rgba(243, 120, 63, 0.7);
    border-color: rgba(243, 120, 63, 0.7);
}

.large{
    background-color: rgba(252, 46, 31, 0.5);
    border-color: rgba(252, 46, 31, 0.5);
}

.popup .leaflet-popup-content-wrapper{
    background-color: rgba(0, 0, 0, 0.7);
    border-color: rgba(0, 0, 0, 0.7);
    border-radius: 20px;
}

.badges{
    font-size: 20px;
    font-weight: 600;
    margin-left: 60px;
    margin-top: 20px;
}
